// subnav

.sub-menu {
    position: absolute;
    width: 220px;
    max-width: 500px;
    @media all and (max-width: 766px){
        width: 100%;
        max-width: 100%;
    }
    height: 0;
    top: 1.5em;
    display: none;
    overflow: hidden;
    transition: 0.25s;
    // background-color: rgba(0, 0, 0, 0.65);
    margin-bottom: 0.75em;
    background-color: #f1f1f1;
    box-shadow: 0 4px 7px 0px rgba(0,0,0,0.25) inset;
    border-left: 2px solid $navy;
    li{
        a{
            color: $navy;

        }
        &:after{
            content: '';
            position: absolute;
            left: 1em;
            bottom: -0.9em;
            width: 50px;
            height: 1px;
            background-color: $navy;
        }
        &:last-of-type{
            &:after{
                display: none;
            }
        }
    }
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0em;
        width: 100%;
        height: 3px;
        background-color: $navy;
    }
    li.menu-item {
        padding: 0 0.75em;
    }
    &.focus{
        display: block;
        height: auto;
    }
    &.is-visible {
        display: block;
        height: auto;
        @media all and (max-width: 766px){
            &.hover{
                display: none;
                height: 0;
                &.focus{
                    display: block;
                    height: auto;
                }
            }
        }
    }
    .visuallyhidden{
        width: 0;
        height: 0;
    }

    @media all and (max-width: 766px){
        position: relative;
    }
}

.sub-menu-expand {
    display: inline-block;
    margin: 0 0 0 0.5em;
    padding: 0;
    font-size: 0.95em;
    transition: 0.45s;
    color: white;
    @media all and (max-width: 766px){
        font-size: 1.1em;
        border: 1px solid black;
        width: 50px;
        height: 50px;
        border-radius: 300px;
        color: black;
    }
    &[aria-expanded='true']{
        transform-origin: center;
        transform: rotateX(180deg);
    }
    sup{

        top: -0.25em;
    }
}